.Historia-h .overlay-1 {
  padding-bottom: 300px; }

.Historia-h .galeria-1 {
  margin-bottom: -300px;
  -webkit-transform: translateY(-300px);
  transform: translateY(-300px); }

.Historia-ativa {
  padding: 40px 0;
  background-color: #A68951; }

.Historia-ativa__title h2 {
  font-size: 34px;
  color: #fff;
  font-weight: normal;
  margin-top: 0;
  text-transform: uppercase; }

.Historia-ativa__toggle {
  position: relative;
  width: 400px;
  max-width: 100%;
  display: flex;
  justify-content: space-between; }

.Historia-ativa__toggle:before {
  content: '';
  width: calc(100% - 80px);
  height: 2px;
  position: absolute;
  top: 20px;
  left: 20px;
  background-color: #fff; }

.Historia-ativa__toggle button {
  background-color: transparent;
  color: #fff;
  border: none;
  position: relative;
  padding-top: 50px;
  font-size: 26px; }

.Historia-ativa__toggle button:before {
  content: '';
  width: 40px;
  height: 40px;
  border: 2px solid #fff;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  margin: 0 auto;
  background-color: #A68951;
  -webkit-transition: background-color 0.3s;
  transition: background-color 0.3s; }

.Historia-ativa__toggle button.selected:before {
  background-color: #0A221D; }

.Historia-ativa .container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
